/* // Messages Page Stylesheet
// ======================================== */

/* NOTE:
  • This file overrides the default styles for RBC Calendar
*/

/* Imports */
@import url(../settings/borders.module.css);
@import url(../settings/colors.module.css);
@import url(../settings/typography.module.css);
@import url(../settings/spacers.module.css);

/* Structure */
.rbc-calendar {
  background-color: var(--unity-t-white-base);
  -webkit-box-shadow: 0 2px 8px 0 rgba(0,0,0,0.10);
          box-shadow: 0 2px 8px 0 rgba(0,0,0,0.10);
  border-radius: var(--unity-radius-base);
}

/* Header */
.rbc-toolbar {
  background: #FAFBFC;
  border: var(--unity-border-base);
  border-top-left-radius: var(--unity-radius-base);
  border-top-right-radius: var(--unity-radius-base);
  border-bottom: none;
  margin-bottom: var(--unity-spacer-0);
  padding: var(--unity-spacer-16);
}

.rbc-toolbar button {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  color: var(--unity-t-white-base);
}

.rbc-toolbar button:hover,
.rbc-toolbar button:active,
.rbc-toolbar button:focus,
.rbc-toolbar button:active:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: var(--color-primary-hover);
  border-color: var(--color-primary-hover);
  color: var(--unity-t-white-base);
}

.rbc-btn-group button {
  background-color: transparent;
  border: var(--unity-border-base);
  border-radius: var(--unity-radius-base);
  color: var(--unity-t-gray-dark);
  cursor: pointer;
  font-size: var(--unity-font-10);
  font-weight: var(--unity-font-bold);
  text-transform: uppercase;
}

.rbc-btn-group button:active:focus {
  background-color: var(--unity-b-blue-base);
  border-color: var(--unity-b-blue-base);
}

.rbc-btn-group button.rbc-active,
.rbc-btn-group button.rbc-active:hover,
.rbc-btn-group button:hover,
.rbc-btn-group button.rbc-active:focus,
.rbc-btn-group button:focus,
.rbc-btn-group button.rbc-active:active,
.rbc-btn-group button:active {
  background-color: var(--unity-t-white-base);
  border: var(--unity-border-base);
  -webkit-box-shadow: none;
          box-shadow: none;
  color: var(--unity-b-blue-base);
  outline: none;
}

.rbc-toolbar-workforce .rbc-toolbar-label {
  left: 0;
  position: absolute;
  right: 0;
}

.rbc-toolbar-workforce .rbc-btn-group {
  position: relative;
  z-index: 1;
}

.rbc-toolbar-workforce .rbc-toolbar-action {
  margin-left: auto;
  position: relative;
  z-index: 1;
}

.rbc-toolbar-label {
  font-weight: var(--unity-font-bold);
}

/* Body Month */
.rbc-month-view {
  border: var(--unity-border-base);
  border-bottom-left-radius: var(--unity-radius-base);
  border-bottom-right-radius: var(--unity-radius-base);
}

.rbc-month-header,
.rbc-header {
  background-color: var(--unity-s-gray-light);
}

.rbc-header,
.rbc-agenda-view table.rbc-agenda-table thead > tr > th {
  border-bottom: var(--unity-border-base);
  padding: var(--unity-spacer-8);
}

.rbc-header span {
  color: var(--unity-t-gray-dark);
  font-size: var(--unity-font-12);
}

.rbc-month-row {
  overflow: visible !important;
}


.rbc-day-bg {
  -webkit-transition: background-color 0.35s ease;
  -o-transition: background-color 0.35s ease;
  transition: background-color 0.35s ease;
}

.rbc-day-bg:hover {
  background-color: var(--unity-s-gray-light);
  cursor: pointer;
}

/* Body Week */
.rbc-label {
  color: var(--unity-t-gray-dark);
  display: inline-block;
  font-size: var(--unity-font-12);
  padding-top: var(--unity-spacer-8);
  padding-right: 20px;
}

/* Body Week Border Resets */
.rbc-time-view {
  border: var(--unity-border-base) !important;
}

.rbc-time-header.rbc-overflowing {
  border: none;
}

.rbc-time-content {
  border-top: 2px solid var(--unity-s-gray-medium);
}

.rbc-timeslot-group {
  border-bottom: var(--unity-border-base);
}

.rbc-time-header-gutter,
.rbc-time-slot {
  background-color: var(--unity-s-gray-light);
}

.rbc-day-slot .rbc-time-slot {
  border-top: var(--unity-border-base);
  opacity: 0.25;
}

.rbc-day-slot .rbc-time-slot:first-child {
  border: none;
}

.rbc-time-content > * + * > * {
  border-left: var(--unity-border-base);
}

/* Day Background */
.rbc-today {
  background-color: var(--unity-t-white-base);
}

.rbc-off-range-bg {
  background-color: var(--unity-s-gray-alt-2);
}

.rbc-day-slot .rbc-events-container {
  width: 100%;
}

/* Day Date */
.rbc-date-cell {
  padding: var(--unity-spacer-4);
}
.rbc-date-cell a {
  font-size: var(--unity-font-12);
  margin-right: var(--unity-spacer-4);
  display: inline-block;
}

.rbc-now a {
  background-color: var(--unity-b-green-base);
  border-radius: var(--unity-radius-circle);
  color: var(--unity-t-white-base);
  padding: var(--unity-spacer-4);
}

.rbc-off-range {
  color: var(--unity-t-gray-base);
}

/* Agenda */
.rbc-agenda-view {
  border: var(--unity-border-base);
  border-bottom-left-radius: var(--unity-radius-base);
  border-bottom-right-radius: var(--unity-radius-base);
}

.rbc-agenda-table {
  border: none;
}

.rbc-agenda-view table.rbc-agenda-table {
  border: none;
  border-bottom: var(--unity-border-base);
}

.rbc-agenda-view table.rbc-agenda-table thead > tr > th {
  background-color: var(--unity-s-gray-light);
  color: var(--unity-t-gray-dark);
  font-size: var(--unity-font-12);
}

.rbc-header + .rbc-header {
  border-left: var(--unity-border-base);
}

.rbc-agenda-view table.rbc-agenda-table tbody > tr > td {
  font-size: var(--unity-font-16);
  padding-top: var(--unity-spacer-16);
  padding-bottom: var(--unity-spacer-16);
  padding-right: 10.5px;
}

/* Event */
.rbc-event-label {
  display: none;
}

.rbc-day-slot .rbc-event {
  overflow: visible !important;
}

.rbc-event {
  border-radius: var(--unity-radius-base);
  margin-bottom: var(--unity-spacer-4);
  margin-left: var(--unity-spacer-4);
  padding: var(--unity-spacer-0);
  width: calc(100% - 8px) !important;
}

.rbc-event.rbc-selected {
  background-color: transparent;
  position: relative;
}

.rbc-day-slot .rbc-event {
  border-color: transparent;
}

.rbc-day-slot .rbc-event.rbc-selected {
  border-color: transparent;
}

.rbc-event-content .title {
  border-radius: var(--unity-radius-base);
  color: var(--unity-t-white-base);
  font-size: var(--unity-font-14);
  padding: var(--unity-spacer-0) var(--unity-spacer-4);
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.rbc-show-more {
  font-size: var(--unity-font-12);
  padding-left: var(--unity-spacer-4);
}

/* Time Indicator */
.rbc-current-time-indicator {
  background-color: var(--unity-b-red-base);
  height: 2px;
  left: 70px !important; /* Offset time row */
  width: 100% !important;
}

/* Popout */
.calendar__popout {
  -webkit-transition: bottom 0.35s ease;
  -o-transition: bottom 0.35s ease;
  transition: bottom 0.35s ease;
  left: 8px;
  margin: 0;
  width: 200px;
  bottom: 100%;
}

.rbc-selected .calendar__popout {
  bottom: calc(100% + 16px);
  opacity: 1;
  position: absolute;
  visibility: visible;
  z-index: 101;
}

.calendar__popout .header {
  border-bottom: var(--unity-border-base);
  padding: var(--unity-spacer-8);
}

.calendar__popout .header .header__title {
  font-size: var(--unity-font-14);
}

.calendar__popout .body {
  padding: var(--unity-spacer-8);
  padding-bottom: var(--unity-spacer-0);
}

.calendar__popout .body__group {
  margin-bottom: var(--unity-spacer-8);
}

.calendar__popout .body p {
  white-space: normal;
}

/* Modal */
.closed {
  display: none;
}


/* Workforce Event */
.workforce__event {
  background-color: var(--unity-s-blue-light);
  text-align: center;
}

.workforce__event .title {
  color: var(--unity-b-blue-base);
  font-weight: var(--unity-font-bold);
}

/* Selected Event */
.workforce__event .selected {
  background-color: var(--unity-b-orange-light);
}

.workforce__event .title.selected {
  color: var(--unity-b-orange-base);
}

/* Hide Past Events */
.workforce__event__hidden {
  display: none;
}



